@import "cards";

/* tabs */
.nav-tabs {
    margin-bottom: 30px;
    .nav-item {
        border-bottom: 2px solid var(--info);
        position: relative;
        flex: 1;
        &:hover {
            background-color: #f0f1f2;
            @media screen and (max-width: 820px) {
                background-color: var(--white);
            }
        }

        .nav-link {
            border: none !important;
            font-weight: 500;
            cursor: default;
            font-size: 16px;
            color: var(--info);
            &:hover {
                border-color: none !important;
            }

            &.active {
                border: none;
                color: black;
                font-size: 20px;
                &:after {
                    content: "";
                    position: absolute;
                    top: 106%;
                    left: 50%;
                    transform: translate(-50%, -50%) rotate(-135deg);
                    width: 20px;
                    border: 2px solid var(--info);
                    height: 20px;
                    border-bottom: none;
                    border-right: none;
                    @media screen and (max-width: 820px) {
                        top: 103%;
                    }
                }
                &:before {
                    position: absolute;
                    content: "";
                    border-top: 4px solid var(--white);
                    transform: translate(-50%, -50%);
                    top: 100%;
                    left: 50%;
                    width: 26px;
                    @media screen and (max-width: 820px) {
                        border-top: 6px solid var(--white);
                    }
                }
                &:hover {
                    background: transparent;
                }
            }
        }
    }
}

/* Arrows */
.slick-prev,
.slick-next {
    font-size: 0;
    line-height: 0;
    position: absolute;
    top: 50%;
    display: block;
    width: 20px;
    height: 20px;
    padding: 0;
    transform: translate(0, -50%);
    cursor: pointer;
    color: transparent;
    border: none;
    outline: none;
    background: transparent;
}
.slick-prev {
    left: -40px;
    &:before {
        content: ".";
        background: url("/themes/pippip/assets/img/chevron-left.svg") no-repeat fixed center;
        background-size: 40px 40px;
        position: absolute;
        top: 0;
        left: -20px;
        padding: 27px;
        width: 44px;
        height: 44px;
        opacity: 0.75;
    }
}
.slick-prev:hover:before {
    content: ".";
    background: url("/themes/pippip/assets/img/chevron-left-blue.svg") no-repeat fixed center;
    background-size: 40px 40px;
    position: absolute;
    border: 2px solid var(--info);
    border-radius: 50%;
    top: 0;
    left: -20px;
    padding: 25px;
    width: 44px;
    height: 44px;
    opacity: 0.75;
}

.slick-next {
    right: -40px;
    &:before {
        content: ".";
        background: url("/themes/pippip/assets/img/chevron-right.svg") no-repeat fixed center;
        background-size: 40px 40px;
        position: absolute;
        top: 0;
        left: 0;
        padding: 25px;
        width: 44px;
        height: 44px;
        opacity: 0.75;
    }
}
.slick-next:hover:before {
    content: ".";
    background: url("/themes/pippip/assets/img/chevron-right-blue.svg") no-repeat fixed center;
    background-size: 40px 40px;
    position: absolute;
    border: 2px solid var(--info);
    border-radius: 50%;
    padding: 25px;
    top: -2px;
    left: -2px;
    width: 44px;
    height: 44px;
    opacity: 0.75;
}

/* Dots */
.slick-slider {
    padding-bottom: 20px;
}
.slick-dotted.slick-slider {
    margin-bottom: 30px;
}

.slick-dots {
    li {
        position: relative;
        display: inline-block;
        width: 20px;
        height: 20px;
        margin: 0 5px;
        padding: 0;
        cursor: pointer;
        button {
            &:before {
                font-size: 10px;
                line-height: 10px;
                width: 13px;
                height: 12px;
                border-radius: 50%;
                content: "â€¢";
                opacity: 0.25;
                border: 2px solid var(--info);
                color: var(--white);
            }
        }
    }
}

.slick-dots li.slick-active button:before {
    opacity: 0.75;
    color: var(--info);
}

// cursor

#cursor {
    background-color: transparent;
    border: 2px solid var(--info);
    height: 30px;
    width: 30px;
    display: none;
    border-radius: 50%;
    position: absolute;
    z-index: 1;
}
#cursor.visible {
    display: block;
}
